                v-model="basisContent"
                @input="descInput6"
                placeholder="请输入研究基础..."
              ></el-input>
              <div class="contentCount">
                <span>{{basisCount}}</span>
                <span>/500</span>
              </div>-->
              <dt-textarea :value.sync="basisContent" placeholder="请输入研究基础..." :maxNumber="2000"></dt-textarea>
            </el-form-item>
          </div>
