      left: 40px;
      top: 14px;
      z-index: 10;
      font-size: 18px;
      font-weight: 700;
    }
    .txt2 {
      position: absolute;
      right: 180px;
      // bottom: 80px;
      z-index: 10;
    }
    .txt21 {
      position: absolute;
      right: 50%;
      // bottom: 80px;
      z-index: 10;
    }
    .txt3 {
      position: absolute;
      right: 100px;
      // bottom: 45px;
      z-index: 10;
    }
    .txt4 {
      position: absolute;
      right: 88px;
      bottom: 10px;
      z-index: 10;
      font-size: 16px;
      color: #b3b3b3;
    }
    .txt5 {
      position: absolute;
      left: 40px;
      top: 60px;
      z-index: 10;
      padding-right: 100px;
      line-height: 40px;
      text-indent: 2em;
    }
  }
}
</style>





